.post {
   display: flex;
   column-gap: 40px;
   border: solid black 1px;
   padding: 10px 20px;
   border-radius: 10px 20px;
   width: fit-content;
   align-items: center;
   font-size: 18px;
   margin: 20px 0 0 0;
}

.post img {
   width: 40px;
   height: 40px;
   border-radius: 50%;
}